import React, { useCallback, useState, useEffect,useRef } from 'react';
import { Form, Input, Button } from 'antd';
import { useSelector, useDispatch } from 'react-redux';
import { ADD_POST_REQUEST, UPLOAD_IMAGES_REQUEST, REMOVE_IMAGE } from '../reducers/post';

const PostForm = () => {
  const dispatch = useDispatch();
  const [text, setText] = useState('');
  const { imagePaths, isAddingPost, postAdded } = useSelector(state => state.post);
  const imageInput  = useRef();

  useEffect(() => {
    if (postAdded) {
      setText('');
    }
  }, [postAdded]);


  const onSubmitForm = useCallback((e) => {
    e.preventDefault();
    if(!text || !text.trim()){
      return alert(' ');
    }

    const formData = new FormData();
    imagePaths.forEach((i) => {
      formData.append('image', i);
    });
    formData.append('content', text);

    dispatch({
      type: ADD_POST_REQUEST,
      data: formData,
    });
  }, [text,imagePaths]);

  const onChangeText = useCallback((e) => {
    setText(e.target.value);
  }, []);

  const onChangeImages = useCallback((e) => {  //   
    console.log(e.target.files);
    const imageFormData = new FormData();
    [].forEach.call(e.target.files, (f) => {
      imageFormData.append('image', f);  // AJAX  SPA         
    });
    dispatch({
      type: UPLOAD_IMAGES_REQUEST,
      data: imageFormData,
    });
  }, []);

  const onClickImageUpload = useCallback(() => {
    imageInput.current.click(); //      input ref imageInput    
  }, [imageInput.current]);

  const onRemoveImage = useCallback(index => () => {  // onRemoveImage(i)  () => () => {} // isAddingPost () => {}
    dispatch({
      type: REMOVE_IMAGE,
      index,
    });
  }, []);

  return (
    <Form style={{ margin: '10px 0 20px' }} encType="multipart/form-data" onSubmit={onSubmitForm}>
      <Input.TextArea maxLength={140} placeholder="   ?" value={text} onChange={onChangeText} />
      <div>
        <input type="file" multiple hidden ref={imageInput} onChange={onChangeImages} />
        <Button onClick={onClickImageUpload}> </Button>
        <Button type="primary" style={{ float: 'right' }} htmlType="submit" loading={isAddingPost}></Button>
      </div>
      <div>
        {imagePaths.map((v,i) => (
          <div key={v} style={{ display: 'inline-block' }}>
            <img src={`http://localhost:2323/${v}`} style={{ width: '200px' }} alt={v} />
            <div>
              <Button onClick={onRemoveImage(i)}></Button>
            </div>
          </div>
        ))}
      </div>
    </Form>
  );
};

export default PostForm;